<script>
  import { credentials } from "../../credentials.js";
  import { onMount } from "svelte";
  import {
    userName,
    userPic,
    gapiInstance,
    showEntry,
    showSettings,
    showSummary
  } from "../store/store.js";
  import LoadingSpinner from "./LoadingSpinner.svelte";
  import { fade } from "svelte/transition";

  let showErrorScreen = false;
  let signingInPromise;
  let auth2;

  onMount(() => {
    const script = document.createElement("script");
    script.src = "https://apis.google.com/js/api.js";

    script.onload = () => {
      gapi.load("client:auth2", initClient);
    };

    script.async = true;
    script.defer = true;
    document.body.appendChild(script);
  });

  function initClient() {
    signingInPromise = gapi.client
      .init({
        clientID: credentials.CLIENT_ID,
        apiKey: credentials.API_KEY,
        scope: credentials.SCOPES,
        discoveryDocs: credentials.DISCOVERY_DOCS
      })
      .then(() => {
        gapi.load("auth2", initAuth2);
      })
      .catch(e => {
        showErrorScreen = true;
      });
  }

  function initAuth2() {
    gapi.auth2
      .init({
        clientID: credentials.CLIENT_ID,
        scope: credentials.SCOPES
      })
      .then(() => {
        updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
      });
  }

  function updateSigninStatus(signedIn) {
    if (signedIn) {
      console.log("Signed in automatically");

      gapiInstance.set(gapi);
      userName.set(
        gapi.auth2
          .getAuthInstance()
          .currentUser.get()
          .getBasicProfile()
          .getName()
      );
      userPic.set(
        gapi.auth2
          .getAuthInstance()
          .currentUser.get()
          .getBasicProfile()
          .getImageUrl()
      );
    } else {
      userName.set();
      userPic.set();
    }
  }
</script>

<svelte:head>
  <meta name="google-signin-client_id" content={credentials.CLIENT_ID} />
</svelte:head>

{#await signingInPromise}
  <div
    transition:fade
    class="w-full h-screen bg-black opacity-75 flex items-center justify-center
    absolute"
    on:click>
    <LoadingSpinner text="Signing in" />
  </div>
{:then}
  <div />
{/await}
{#if showErrorScreen}
  <div
    transition:fade
    class="w-full h-screen bg-black opacity-75 absolute"
    on:click />
  <div
    class="w-full h-screen flex flex-col items-center justify-center absolute">
    <p class="w-1/2 text-center text-secondaryColor mb-4">
      Error signing in. Please try again.
    </p>
    <button
      class="py-2 w-1/2 rounded-lg bg-accentColor2 text-white font-medium"
      on:click={() => {
        showEntry.set(false);
        showSummary.set(false);
        showSettings.set(true);
        showErrorScreen = false;
      }}>
      Go to Settings
    </button>
  </div>
{/if}
